<script>
import XLSX from "xlsx";
export default {
  props: { selecton: Function, selectMarkers: Object, selectFunc: Boolean },
  data() {
    return {
      active: null,
      table: false,
    };
  },
  methods: {
    ExportExcel(type, fn, dl) {
      const elt = this.$refs.exportable_table;
      const wb = XLSX.utils.table_to_book(elt, { sheet: "Sheet JS" });

      return dl
        ? XLSX.write(wb, { bookType: type, bookSST: true, type: "base64" })
        : XLSX.writeFile(
            wb,
            fn || ("data" + "." || "SheetJSTableExport.") + (type || "xlsx")
          );
    },
    activeBtn(item) {
      if (this.active === item) {
        this.active = null;
      } else {
        this.active = item;
      }
    },
  },
  //   mounted() {
  //     console.log(this.selectFunc);
  //   },
};
</script>

