<template>
  <!-- <button v-for="n in markers" :key="n.id" @click="onClick(n)">
    #{{ n.id }}
  </button> -->
  <yandex-map
    ref="map"
    :coords="[55.72, 37.65]"
    zoom="8"
    style="width: 100%; height: 100vh"
    :controls="[]"
  >
    <ymap-marker
      v-for="n in markers2"
      :key="n.id"
      :marker-id="n.id"
      marker-type="Rectangle"
      :marker-stroke="{ opacity: 0.5 }"
      :marker-fill="{
        color: n.colorRec(),
      }"
      :coords="n.coord"
      :balloon="{ body: selectFunc ? '' : n.text }"
      @click="selectFunc ? selectMarker(n) : ''"
    >
    </ymap-marker>
  </yandex-map>

  <MapMenu
    :selecton="selectOn"
    :selectFunc="selectFunc"
    :selectMarkers="selectMarkers"
  />
</template>
<script>
import { yandexMap, ymapMarker } from "vue-yandex-maps";
import axios from "axios";
import MapMenu from "../components/MapMenu.vue";

export default {
  name: "App",
  components: {
    yandexMap,
    ymapMarker,
    MapMenu,
  },
  data() {
    return {
      url: "http://78.47.77.103:8081/MoscowData/GetByID/",
      selectMarkers: {
        volumeMarkers: 0,
        adm_name: [],
        cnt_home: 0,
        cnt_job: 0,
        cnt_day: 0,
        cnt_move: 0,
        served: 0,
        area: 0,
      },
      selectFunc: false,
      markers2: [],
    };
  },
  methods: {
    selectMarker(e) {
      if (e.active === false) {
        e.clickMin = "#00beff";
        e.clickMin02 = "#00beff";
        e.clickMin03 = "#00beff";
        e.clickMin04 = "#00beff";
        e.clickMiddle = "#00beff";
        e.clickMiddle01 = "#00beff";
        e.clickMiddle02 = "#00beff";
        e.clickMiddle03 = "#00beff";
        e.clickMiddle04 = "#00beff";
        e.clickMiddle05 = "#00beff";
        e.active = true;
        this.selectMarkers.volumeMarkers += 1;
        if (this.selectMarkers.adm_name.indexOf(e.res.data.adm_name) === -1) {
          this.selectMarkers.adm_name.push(e.res.data.adm_name);
        }
        this.selectMarkers.cnt_home += +e.res.data.customers_cnt_home;
        this.selectMarkers.cnt_job += +e.res.data.customers_cnt_job;
        this.selectMarkers.cnt_day += +e.res.data.customers_cnt_day;
        this.selectMarkers.cnt_move += +e.res.data.customers_cnt_move;
        this.selectMarkers.served += +e.served;
        this.selectMarkers.area += +e.res.data.area;
      } else {
        e.active = false;
        e.clickMin = "rgba(196, 32, 0, 0.5)";
        e.clickMin02 = "rgba(205, 56, 0, 0.5)";
        e.clickMin03 = "rgba(222, 128, 1, 0.5)";
        e.clickMin04 = "rgba(255, 198, 38, 0.5)";
        e.clickMiddle = "rgba(255, 228, 4, 0.5)";
        e.clickMiddle01 = "rgba(192, 225, 9, 0.5)";
        e.clickMiddle02 = "rgba(84, 191, 48, 0.5)";
        e.clickMiddle03 = "rgba(26, 177, 15, 0.5)";
        e.clickMiddle04 = "rgba(18, 112, 11, 0.5)";
        e.clickMiddle05 = "rgba(11, 69, 7, 0.5)";
        this.selectMarkers.volumeMarkers -= 1;
        this.selectMarkers.cnt_home -= +e.res.data.customers_cnt_home;
        this.selectMarkers.cnt_job -= +e.res.data.customers_cnt_job;
        this.selectMarkers.cnt_day -= +e.res.data.customers_cnt_day;
        this.selectMarkers.cnt_move -= +e.res.data.customers_cnt_move;
        this.selectMarkers.served -= +e.served;
        this.selectMarkers.area -= +e.res.data.area;
      }
    },
    selectOn() {
      this.selectFunc = !this.selectFunc;
    },
    async fetchData() {
      for (let i = 0; i <= 550; i++) {
        try {
          const res = await axios.get(this.url + i);

          //   for (let item of res.data) {
          this.markers2.push({
            coord: [
              res.data.geometry_1.split(",").map((string) => +string),
              res.data.geometry_2.split(",").map((string) => +string),
            ],
            id: i,
            res: res,
            colorRec: function () {
              if (res.data.predictions <= 0.1) {
                return this.clickMin;
              } else if (res.data.predictions <= 0.2) {
                return this.clickMin02;
              } else if (res.data.predictions <= 0.3) {
                return this.clickMin03;
              } else if (res.data.predictions <= 0.4) {
                return this.clickMin04;
              } else if (res.data.predictions <= 0.5) {
                return this.clickMiddle;
              } else if (res.data.predictions <= 0.6) {
                return this.clickMiddle01;
              } else if (res.data.predictions <= 0.7) {
                return this.clickMiddle02;
              } else if (res.data.predictions <= 0.8) {
                return this.clickMiddle03;
              } else if (res.data.predictions <= 0.9) {
                return this.clickMiddle04;
              } else if (res.data.predictions <= 1) {
                return this.clickMiddle05;
              } else if (res.data.served == 0) {
                return this.servedColor;
              }
            },
            clickMin: "rgba(196, 32, 0, 0.5)",
            clickMin02: "rgba(205, 56, 0, 0.5)",
            clickMin03: "rgba(222, 128, 1, 0.5)",
            clickMin04: "rgba(255, 198, 38, 0.5)",
            clickMiddle: "rgba(255, 228, 4, 0.5)",
            clickMiddle01: "rgba(192, 225, 9, 0.5)",
            clickMiddle02: "rgba(84, 191, 48, 0.5)",
            clickMiddle03: "rgba(26, 177, 15, 0.5)",
            clickMiddle04: "rgba(18, 112, 11, 0.5)",
            clickMiddle05: "rgba(11, 69, 7, 0.5)",
            servedColor: "#1999ff",
            active: false,
            text: `
                  <h2 class="red">Район ${res.data.adm_name}</h2>
                  <h4>Округ ${res.data.okrug_name}</h4>
                  <p>ID административного деления  : ${res.data.adm_zid}</p>
                  <p>ID округа : ${res.data.okrug_id}</p>
                  <p>Количество человек, проживающих на территории сектора: ${
                    res.data.customers_cnt_home
                  }</p>
                  <p>Количество человек, работающих на территории сектора: ${
                    res.data.customers_cnt_job
                  }</p>
                  <p>Количество человек, которые находились на территории сектора по будням в дневное время чаще, чем в любых других секторах, в рассматриваемый месяц: ${
                    res.data.customers_cnt_day
                  }</p>
                  <p>Количество человек, проживающих на территории сектора и имеющих разъездную работу: ${
                    res.data.customers_cnt_move
                  }</p>
                  <p>обслуживается ли поликлинникой(искомый признак): ${
                    res.data.served === 0
                      ? "Квадрат не обслуживается"
                      : "Квадрат обслуживается"
                  }</p>

              `,
          });
          //   }
        } catch (error) {
          return error;
        }
      }
    },
  },

  mounted() {
    this.fetchData();
  },
};
</script>

<style scoped lang="scss">
.ymaps-2-1-79-gotoymaps__container {
  display: none !important;
}
</style>
